<template>
  <div>
    <p>매치 페이지</p>
    <table>
      <tr>
        <th>테니스 코트</th>
        <td>
          <select v-model="schedule.courtIdx">
            <option v-for="court in courts" :value="court.idx">{{court.name}}</option>
          </select>
        </td>
        <td>
          <v-autocomplete
            :items="users"
            item-text="김수환"
            @change="onChanged"
          ></v-autocomplete>
        </td>
      </tr>
    </table>

    <button @click="onSave">저장</button>
  </div>
</template>

<script>
  import axios from "axios";

  export default {
    name: 'ScheduleDetail',
    components: {
    },
    data () {
      return {
        schedule: {
          courtIdx: '',
          user: []
        },
        courts: [],
        users: []
      }
    },
    created() {
      this.getCourtsInfo()
    },
    methods: {
      getCourtsInfo () {
        axios.get('http://localhost:9000/courts').then(res => {
          this.courts = res.data
        })
      },
      getUsersInfo () {
        axios.get('http://localhost:9000/users').then(res => {
          this.users = res.data
        })
      },
      onChanged () {
        console.log('changed')
      },
      onSave () {
        console.log('schedule data:', this.schedule)
      }
    }
  }
</script>

<style scoped>

</style>



